.price {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 0 0 20px;
  @extend %font-text;

  &__title {
    color: $black-color;
  }

  &__cost {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
  }

  & :nth-child(n) {
    margin: 0;
  }
}
